.app-header {
    background-color: #222;
    height: 180px;
    padding: 10px 20px;
    color: white;
}

.app-header .input-options {
    margin: 0 auto;
    padding: 5px;
    width: 430px;
    height: 100px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: column nowrap;
        flex-flow: column nowrap;
    -ms-flex-align: start;
        align-items: flex-start;
    -ms-flex-pack: justify;
        justify-content: space-between;
    position: relative;
}

.app-header .game-options-form {
    -webkit-transition: opacity 0.4s;
    -o-transition: opacity 0.4s;
    transition: opacity 0.4s;
}

.app-header .game-options-form.players,
.app-header .game-options-form.cards {
    display: -ms-flexbox;
    display: flex;
    width: 100%;
    -ms-flex-pack: justify;
        justify-content: space-between;
    opacity: 1;
}

.app-header .game-options-form.disabled  {
    opacity: 0;
}

.app-header .game-options-form input {
    margin: 0 10px;
    min-width: 80px;
    text-align: center;
    border-radius: 3px;
    border: none;
}

.app-header .submit-game-options {
    margin: 0 auto;
    -webkit-transition: opacity 0.4s;
    -o-transition: opacity 0.4s;
    transition: opacity 0.4s;
    opacity: 1;
}

.app-header .submit-game-options:disabled {
    opacity: 0;
}

.app-header .error {
    color: red;
    position: absolute;
    bottom: 0px;
}


